/*@import-normalize;*/

/*https://yarnpkg.com/en/package/normalize.css*/

* {
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: border-box;
}

html {
    font-family: sans-serif;
    line-height: 1.15;
}

body {
    overflow: scroll;
    margin: 0;
    font-family: "Segoe UI", "Helvetica Neue", "Arial", serif;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.6;
    color: #212529;
    text-align: left;
    background-color: #fff;
    overflow-x: hidden;
    padding: 69.75px 0 0;
}

@media (min-width: 1920px) {
    html {
        font-size: 17px;
    }
}
